.me{
  height: 200px;
  border-radius: 50%;
  margin: 50px;

}
.about > * {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}
.about{
  display:-ms-flexbox;
  display:flex;
  max-width: 600px;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 auto;
}

.info{
  width: 70%;
  margin: 20px;
}

.tech{
  text-align: center;
  margin: 50px;
}

.link{
  text-align: center;
  width: 70%;
}
@media only screen and (max-width: 800px){
  .about{
    -ms-flex-direction: column;
        flex-direction: column;

  }

  .info{
    margin: 0 auto;
  }
  .link{
    margin: 0 auto;
  }
}



